footer {
    background: rgb(69, 69, 87);
    padding: 8% 5%;
    text-align: center;
    color: #fff;
    width: 100%;
    height: 2.5rem;
  }
  
  .footer-link {
    color: #fff;
    margin: 10px 30px;
  }
  .footer-link:hover {
    color: rgb(219, 215, 215);
  }
  @media (max-width: 1127px) {
  footer {
    padding: 10% 12%;
  }
  }
  @media(max-width:991px){
    footer {
      padding: 13% 12%;
    }
  }
  
  @media (max-width: 700px) {
  footer {
    padding: 20% 9%;
  }
  }
  @media (max-width: 480px) {
  footer {
    padding: 29% 10%;
  }
  }